import { Component, OnInit } from '@angular/core';
import { Cheque } from '../cheque';
import {MatFormFieldModule} from '@angular/material/form-field';

@Component({
  selector: 'app-principal',
  templateUrl: './principal.component.html',
  styleUrls: ['./principal.component.css']
})
export class PrincipalComponent implements OnInit {
  cheques: Cheque[];
  importe: number;
  gastos: number;
  iva: number;
  total:number;
  superCheque: Cheque;
  constructor() {    
   }

  ngOnInit() {
    this.cheques = new Array();
    this.superCheque = new Cheque(0,0,0);
  }

  public agregar(){
    this.cheques.push(new Cheque(this.importe, this.gastos, this.iva));
    this.calcularTotal();
  }

  public calcularTotal(){
    let total:number = 0;
    this.cheques.forEach(element => {
      this.superCheque.importe += element.importe;
      this.superCheque.gastos += element.gastos;
      this.superCheque.iva += element.iva;
    });
  }
}
